body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.main {
  margin-left: 15rem;
  padding: 2rem;
}
nav {
  height: 100%; /* 100% Full-height */
  width: 15rem; /* 0 width - change this with JavaScript */
  position: fixed; /* Stay in place */
  z-index: 1; /* Stay on top */
  top: 0; /* Stay at the top */
  left: 0;
  background-color: #111; /* Black*/
  overflow-x: hidden; /* Disable horizontal scroll */
  padding-top: 30px; /* Place content 60px from the top */
  transition: 0.5s; /* 0.5 second transition effect to slide in the sidenav */
}

nav a {
  color: white;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: .5rem;
  grid-gap: .25rem;
  gap: .25rem;
  width: 100%;
  cursor: pointer;
  &.active {
    color: #4d4dff;
  }
}

nav ul {
  list-style: none;
}

nav.inactive {
  width: 3.5rem;
}

nav.inactive .search-controller {
  visibility: hidden;
  height: 0;
}

nav.inactive .top-section .toggle-menu {
  top: -.5rem;
  right: 35%;
}

.toggle-menu i.inactive {
  visibility: hidden;
}

.NavMenu {
  display: flex;
  margin-right: .1rem;
  flex-direction: column;
}

.NavMenu a div.inactive {
  visibility: hidden;
}

.NavMenu a i {
  font-size: 1.5rem;
  padding-right: .5rem;
}

.top-section {
  position: relative;
  padding: .5rem;
}

.logo img {
  display: inline-block;
  border-radius: 10px;
  height: 2.5rem;
  width: 2.5rem;
}

.toggle-menu {
  position: absolute;
  top: 50%;
  right: 1rem;
  color: white;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.toggle-menu img {
  font-size: 1.5rem;
  margin: auto;
}

.search-controller {
  padding: .5rem;
}

.search-controller input {
  border: 0;
  outline: none;
  height: 35px;
  background: #333;
  border-radius: 10px;
  display: block;
  width: 80%;
  padding: 0 10px;
}
.recipe-card {
  width: 100%;
  margin: .5rem 0;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
}

.recipe-card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.recipe-card img {
  width: 5.25rem;
  height: 5.25rem;
  position: absolute;
}

.recipe-card .recipe-details {
  padding: 1rem 6rem;

}
.recipe-filters button {
  padding: .5rem;
  margin: .25rem;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  border: none;
  border-radius: 25px;
}

.recipe-filters button:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.recipe-filters button.selected {
  background-color: #ccc;
}

.function-buttons {
  display: flex;
  float: right;
}

.function-buttons a {
  color: black;
  padding: .75rem;
  margin: .25rem;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  border: none;
  border-radius: 25px;
  text-decoration: none;
}

.function-buttons a:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.function-buttons a i {
  color: black;
  padding-right: 5px;
}

.function-buttons .create-button {
  background-color: rgb(71, 181, 0);
}

.function-buttons .create-button:hover {
  background-color: rgb(11, 121, 0);
}

.function-buttons .export-button {
  background-color: rgb(71, 181, 222);
}

.function-buttons .export-button:hover {
  background-color: rgb(11, 121, 162);
}

.function-buttons .clear-button {
  background-color: rgb(187, 166, 222);
}

.function-buttons .clear-button:hover {
  background-color: rgb(127, 106, 162);
}
.recipe-page {
  width: 80%;
  height: 90%;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  padding: .5rem;
  position: fixed;
}

.recipe-header {
  width: 100%;
  height: 30%;
}

.recipe-header img {
  width: 13rem;
  height: 13rem;
  position: absolute;
}

.recipe-title {
  margin-left: 15rem;
}

.recipe-body {
  width: 100%;
  height: 70%;
}

